import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class GeolocationService {

  private geolocationPosition:string = "India";

  constructor(private http: HttpClient) { }

  public  getLocation(): string{
    if (window.navigator && window.navigator.geolocation) {
      window.navigator.geolocation.getCurrentPosition(
          position => {

            this.callApi(position.coords.latitude,position.coords.longitude)
            .then((data)=>
              {
                this.geolocationPosition = data.address.state
              },
              (err)=>{
                console.log("error with https://geocode.xyz : "+ err);
              }
            );
          },
          error => {
              switch (error.code) {
                  case 1:
                      console.log('Permission Denied');
                      break;
                  case 2:
                      console.log('Position Unavailable');
                      break;
                  case 3:
                      console.log('Timeout');
                      break;
              }
          }
      );
  };

  return this.geolocationPosition;

  }

  public callApi( Latitude: number,Longitude: number) : Promise<any>{
    //https://nominatim.openstreetmap.org/reverse?format=json&lat=${Latitude}&lon=${Longitude}&zoom=18&addressdetails=1
    const url = `https://nominatim.openstreetmap.org/reverse?format=json&lat=${Latitude}&lon=${Longitude}&zoom=18&addressdetails=1`
    //Call API

    return this.http.get(url).toPromise()

  }

  public getGeoLocation() : String{
    return this.geolocationPosition
  }
}
